<template>
	<view class="container padding">
		<view class="title flex-row-start">已领礼物</view>
		<view class="item flex-column-start selected" v-for="(item,index) in giftList" :key="index" v-if="item.active">
			<view class="name">{{item.name}}</view>
			<view class="status">{{item.sendState==0?'未发送':'已发送'}}</view>
			<image src="../../static/gift_select.png" class='icon'></image>
		</view>
		<view class="rule flex-row-start" @click="goRule">
			<text>查看使用说明</text>
			<image src="../../static/info_icon.png" class="icon"></image>
		</view>
	</view>
</template>

<script>
	import {http2Https} from "../../common/tools.js"
	export default {
		data() {
			return {
				giftList:[
					{name:"充电费用（价值500元）",id:1,active:false,sendState:0},
					{name:"蓝谷居民积分（价值500元）",id:2,active:false,sendState:0},
					{name:"智慧管家A保代金券*3（价值500元）",id:3,active:false,sendState:0},
				],
				
				orderId:'',
				giftRuleUrl:''
			};
		},
		methods:{
			getGiftRule(){
				this.request("/common/param",{key:"award_use_desc"}).then((res)=>{
					if(res.data){
						this.giftRuleUrl=http2Https(res.data)
					}
				})
			},
			getGift(){
				this.request("/orderAward/showAwardByOrderId",{orderId:this.orderId}).then((res)=>{
					if(res.resultCode==0){
						let _arr=res.data
						_arr.forEach((item)=>{
							for(let i=0;i<this.giftList.length;i++){
								if(item.awardId==this.giftList[i].id){
									this.giftList[i].active=true
									this.giftList[i].sendState=item.sendState
								}
							}
						})
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
				})
			},
			goRule(){
				var _obj={
					url:this.giftRuleUrl
				}
				uni.setStorageSync('webViewInfo', _obj);
				this.perfectNavigateTo({
					url:"/pages/webView/webView"
				})
			},
			
		},
		onLoad(params){
			this.orderId=params.orderId
			this.getGift()
			this.getGiftRule()
		}
	}
</script>

<style lang="stylus">
	.title
		height:44upx;
		font-size:32upx;
		font-weight:600;
		color:rgba(30,30,30,1);
		margin-bottom:45upx
	.item
		position:relative
		width:100%
		width:690upx;
		padding:0upx 30upx
		height:148upx;
		background:rgba(255,255,255,1);
		border-radius:16upx;
		border:2upx solid rgba(203,210,219,1);
		.name
			font-size:32upx;
			font-weight:bold;
		.status
			font-size:24upx
			color:#1F2334
			margin-top:10upx
		&:not(:first-child)
			margin-top:30upx
		&.selected
			border-color:#0075D6
			color:#0075D6
		.icon
			position:absolute
			bottom:-1upx 
			right:0upx
			width:64upx
			height:64upx
	.rule
		margin-top:60upx
		height:40upx;
		font-size:28upx;
		font-family:PingFangSC-Semibold,PingFang SC;
		font-weight:600;
		color:rgba(31,35,52,1);
		.icon
			width:28upx
			height:28upx
			margin-left:20upx
</style>
